*{
    outline: none;
}
:root {
    --tabbar_active_icon: #ff7200;
    --accent: #ff7200;
    --destructive: #ff7200;
    --button_commerce_background:#ff7200;
    --button_primary_background: #ff7200;
    --header_background:#fff;
    --header_background_before_blur: #ececec;
    --header_text: #404040;
    --header_tab_active_text: #212121;
}
.GroupBlockCategory{
    //padding:1px 0;
}

.GroupBlockListAddress{
    padding-bottom:12px;
}

.logoBlock{
    width: 90%;
    margin: 0 auto;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    display: flex;
    padding: 0;
}

.logoBlock img {
    width: 55%;
    max-width: 200px;
    display: block;
    float: left;
    margin:10px 0;
}

.PhoneDiv{
    display: inline-block;
    width:auto;
    float:right;
    padding:5px 15px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    border-radius: 8px;
    border:1px solid #ff7300;
    background-color:#ff7300;
    color:#ffffff;
    margin:10px 0;
    font-size:1em;
}

.PhoneDiv a{
    text-decoration: none;
    color:#ffffff;
    font-weight: 600;
}

.DivCategoryBlock{
    width:100%;
    margin:20px auto;
    color:#FFFFFF;
    text-align: center;
    font-weight: 900;
    padding:20% 0;
    max-height:524px;
    max-width:1032px;
    border-radius: 80;
    font-size:1.4em;
}

.DivBlockDesctopCategoys > div:nth-child(1){
    width:100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.DivBlockDesctopCategoys .DivCategoryBlock{
    width:48%;
    margin:1% auto;
    color:#FFFFFF;
    float:left;
    text-align: center;
    font-weight: 900;
    padding:9% 0;
    max-height:524px;
    max-width:1032px;
    border-radius: 80;
    font-size:1.4em;
}

.CartBlockProducts{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin:auto;
}

.CartBlockProductsDesctop .imgPrewieProduct{
    float: left;
    width: 100px;
}


.CartBlockProductsDesctop .titleProduct{
    float:left;
}

.ProductInCartBlock{
    width:100%;
    float:left;
}

.blockCategoryFlex{
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    flex-wrap: wrap;
    padding:1%;
}

.blockProduct{
    background: #FFFFFF;
    color: #c2c2c2;
    width:100%;
    float: left;
    margin:5px;
    position: relative;
    background: none;
}

.titleProduct{
    padding: 0;
    color:#252622;
    min-height:20px;
    font-weight: 600;
    font-size:18px;
}

.redTextP{
    color: #252622;
    opacity:0.8;
    float:right;
    font-size:16px;
    font-weight: 600;
    /*margin-right:5%;*/
}

.DescrProduct{
    padding: 5px 0;
    padding-bottom:15px;
    font-size:14px;
}

.priceProductBig{
    padding: 0 2px;
    display: block;
    position: absolute;
    bottom:4%;
    width:91%;
}

.priceProductBig:after{
    content: '';
    width:100%;
    display: block;
    clear: both;
}

.priceProductBig span{float:left;margin-top:11px;
    font-weight: 600;font-size: 19px;}

.priceProductBig button{
    border:none;
    background: #ff7300;
    color: #ffffff;
    /*font-family: ALS_Schlange_Sans_Bold;*/
    font-weight: 600;
    font-size:18px;
    float:right;
    border-radius:10px;
    padding:10px 35px;
}


.priceProductBigRecommend button{
    border:none;
    background: #ff7300;
    color: #ffffff;
    /*font-family: ALS_Schlange_Sans_Bold;*/
    font-weight: 600;
    font-size:18px;
    float:none;
    border-radius:10px;
    padding:10px 25px;
    margin:0px auto
}

.DescrProduct:after {
    content: '';
    position: relative;
height:40px;width:100%;
    display: block;
}

.WeightBlock{
    color: #252622;
    margin:15px 0;
    margin-top:20px;
    display: inline-block;
    font-size:1.1em;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    width:auto;
    padding:5px 10px;
    border:1px solid #ff7300;
    clear: both;
}

.WeightBlock::before{
    content: "";
    width: 100%;
    display: block;
    clear: both;
}

.WeightBlock::after{
    content: "";
    width: 100%;
    display: block;
    clear: both;
}

.priceProduct{
    padding: 0 2px;
    display: block;
    color:#252726 !important;
    position: relative;
    bottom:0;
}

.imgPrewieProduct {
    padding: 0;
    text-align:center;
}
.imgPrewieProduct img{width:99%;margin:auto;max-width:200px;}

.CounterInCart{
    float:right;
    margin-top: 0px;
    color:#252622;
}
.CounterInCart div{
    padding: 8px 8px;
    padding-bottom: 10px;
    background: #ff7300;
    display: inline-block;
    width: 25px;
    height: auto;
    text-align: center;
    border-radius: 7px;
    color: #ffffff;
}

.CounterInCart div.minus{
    margin-right:20px;
    font-size:18px;
}
.CounterInCart div.pluse{
    margin-left:20px;
    font-size:18px;
}

.SumItogOneSTepCart{
    text-align:center;
    font-size:1.2em;
    font-weight: 600;
}

.OrangeColor{
    color:#ff7300;
}

.WeightFontBold{
    font-weight: 600;
}


.ModyficateBlock{
    display: flex;
    width:auto;
    position: absolute;
    justify-content: start;
    align-items: start;
}
.ModyficateProduct{
    display:inline-block;
    padding:3px;
    width:28px;
}
.ModyficateProduct img{
    width:100%;
    max-width:28px;
}

.BoxCellSearch{
    width:100%;
    position: fixed !important;z-index:9 !important;border-bottom:1px solid #f2f2f2;
    background:#ffffff !important;
}
.BoxCellSearch .Cell__main{
    padding: 0 !important;
}
.Search{background:#ffffff !important;box-shadow: none !important;padding-left: 0 !important;}
.Search div{background:#ffffff !important;border:none !important;}
.Search div input{color:#333333 !important;}
.Search--ios.Search--default::after{
    height: 0 !important;}

.komboProducts .Cell__bottom {margin-top:0;color:#b5b5b5;}

.titleProductOne{
    max-width:80%;
    float:left;
}

.maxWidths{width:100% !important;}

.clr{height:0.1px;width:100%;clear:both;}

.clrHeight{height:55px;width:100%;}

@media screen and (min-width:360px) {
    .blockProduct {
        width: 100%;
    }
}

@media screen and (min-width:370px) {
    .blockProduct {
        width: 100%;
    }
}
@media screen and (min-width:400px) {
    .blockProduct {
        width: 100%;
    }
}

@media screen and (min-width:600px) {
    .blockProduct {
        width: 42.2%;
    }
    .priceProductBig button{
        padding: 5px 25px;
    }
    .priceProductBig span{
        margin-top: 5px;
    }
}

@media screen and (min-width:700px) {
    .blockProduct {
        width: 44%;
    }
}