.ButtonPanelNavigation{
    width:96%;
    display: block;
    background: #FFFFFF;
    padding:1% 2%;
}

.ButtonPanelNavigation button{
    border:1px solid #f2f2f2;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    background: #ffffff;
    color:#333333;
    font-size:16px;
    padding:5px 10px;
}

.ButtonPanelNavigation button.activeButons{
    background: #ff7300;
    color:#fff;
}

.AndroidCLASSCSS .PanelHeader__left{
    display:none;
}
.AndroidCLASSCSS .PanelHeader__right{
    display:none;
}

#dilivery{
    background: #FFFFFF;
}

.DescrProduct2{
    padding: 5px 0;
    padding-bottom:15px;
    font-size:12px;
}


.DescrProduct2:after{
    content: '';
    position: relative;
    width:100%;
    display: block;
    height:10px;
}

.imgPrewieProduct2 {
    padding: 0;
    text-align: center;
    width: 50px;
    float: left;
    margin-right: 10px;
}
.imgPrewieProduct2 img{width:99%;margin:auto;max-width:200px;}


.old_price_style {
    position: relative;
    color: #7f94a1;
    text-decoration: none;
}

.old_price_style:before {
    border-bottom: 0.1em solid red;
    position: absolute;
    content: "";
    width: 100%;
    height: 50%;
    transform: rotate(-11deg);
}



/*Доп товары корзина*/

.BlockOneDopProductCart{
    min-width:180px;
    max-width:180px;
    margin:0 7px;
    border:1px solid #f2f2f2;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    border-radius: 8px;
}

.imgDopProductBlock img{
    width:90%;
    max-width:200px;
    margin:auto;
    display: block;
}
.titleDopProductBlock{
    text-align: center;
    min-height: 20px;
}

.priceDopProductBlock{
    text-align: center;
    color:#ff7300;
}

.buttonDopProductBlock{
    margin-top:10px;
    margin-bottom:20px;
}

.streetCellChecker{
    border-bottom:1px solid #fafafa;
}

.streetCellChecker:hover{
    background: #fafafa;
}

.streetCellChecker .nameCity{
    font-weight: Bold;
}

.searckBoxses .Search--android.Search--default{
    border: 1px solid #eeeeee;
}

.searckBoxses .Search--ios.Search--default{
    border: 1px solid #eeeeee;
}